	},
	methods: {
		//--------------------------读取车票信息--------------------------
		getTickerInfo() {
			var that = this;
			//读取车票信息
			uni.getStorage({
				key: 'ticketDate',
				success: function(data) {
					that.orderInfo = data.data;
					// console.log('订单数据', that.orderInfo)
				},
				fail() {
					uni.showToast({
						title: '获取信息失败',
						icon: 'none'
					})
				}
			})
		},
		//--------------------------读取用户信息--------------------------
		getUserInfo() {
			var that = this;
			//读取用户ID
			uni.getStorage({
				key: 'userInfo',
				success: function(data) {
					that.userInfo = data.data;
				}
			})
		},
		//-------------------------------时间转换-------------------------------
		turnDate(date) {
			if (date) {
				var setTime = date.replace('T', ' ');
				return setTime;
			}
		},
		//--------------------------读取乘车人信息--------------------------
		getPassengerInfo() {
			var that = this;
			//读取乘车人信息
			uni.getStorage({
				key: 'passengerList',
				success: function(data) {
					that.passengerInfo = data.data;
					if (that.passengerInfo.length > 0) {
						for (let i = 0; i < that.passengerInfo.length; i++) {
							var type = '';
							if (data.data[i].userType == '儿童') {
								type = 0;
							} else if (data.data[i].userType == '成人') {
								type = 2;
							}
							//拼接id name type
							that.idNameTypeStr += data.data[i].userCodeNum + ',' + data.data[i].userName + ',' + type + '|';

							that.ticketNum++;
							//把儿童票筛选出来
							if (that.passengerInfo.userType == '儿童') {
								that.childrenNum++;
							} else {
								that.adultNum++;
							}
						}
						//把最后面的'｜'去掉
						that.idNameTypeStr = that.idNameTypeStr.substring(0, that.idNameTypeStr.length - 1);
					}
					//-------------------------------读取用户openID-------------------------------
					// that.getOpenID();


					//-------------------------------下单-------------------------------
					that.getOrder();
				},
				fail() {
					uni.showToast({
						title: '获取信息失败',
						icon: 'none'
					})
				}
			})
		},
		//--------------------------读取公众号openid--------------------------
		getOpenID() {
			var that = this;
			uni.getStorage({
				key: 'scenicSpotOpenId',
				success: function(response) {
					// alert('获取id成功');
					that.ctkyOpenID = response.data
					//等待读取用户缓存成功之后再请求接口数据
					that.getOrder();
				},
				fail: function(fail) {
					uni.hideLoading();
					uni.showModal({
						content: '用户未授权',
					})
				}
			})
		},
		//--------------------------隐藏操作--------------------------
		hide(e) {
			if (e == 0) {
				this.hiddenValues = 1;
			} else {
				this.hiddenValues = 0;
			}
		},

		//--------------------------同意购买-点击事件--------------------------
		Selection: function() {
			if (this.channeIndex == 0) {
				this.channeIndex = 1;
			} else {
				this.channeIndex = 0;
			}
		},

		//--------------------------数组提取--------------------------
		screenUser: function() {
			let adult = this.orderInfo.filter(item => {
				return item.userType == '成人';
			})
			let children = this.orderInfo.filter(item => {
				return item.userType == '儿童';
			})

			this.adultIndex = adult.length;
			this.childrenIndex = children.length;
			this.adultTotalPrice = adult.length * this.orderInfo[0].ticketAdultPrice;
			this.childrenTotalPrice = children.length * this.orderInfo[0].ticketChildPrice;
		},
		//--------------------------计时器--------------------------
		getOrder: function() {
			console.log('11111212314432');
			var that = this;
			var timer = null;
			var setTime = that.orderInfo.setTime.replace('T', ' ');
			var companyCode = '';






			// alert(that.ctkyOpenID);
			var data = {
				companyCode: companyCode,
				clientID: that.userInfo.userId, //用户ID
				clientName: that.userInfo.nickname, //用户名
				phoneNumber: that.userInfo.phoneNumber, //手机号码
			
				scheduleCompanyCode: that.orderInfo.scheduleCompanyCode, //班次代码
				executeScheduleID: that.orderInfo.executeScheduleID, //班次ID
				startSiteID: that.orderInfo.startSiteID, //上车点ID
				endSiteID: that.orderInfo.endSiteID, //下车点ID
				startSiteName: that.orderInfo.startStaion, //起点站
				endSiteName: that.orderInfo.endStation, //终点站
				priceID: that.orderInfo.priceID, //价格ID
				setOutTime: setTime, //发车时间
				insuredPrice: that.orderInfo.insurePrice, //保险价格
				carType: that.orderInfo.shuttleType, //班车类型
			
				fullTicket: that.adultNum, //全票人数
				halfTicket: that.childrenNum, //半票人数
				carryChild: that.childrenNum, //携童人数
				idNameType: that.idNameTypeStr, //乘车人信息
				insured: that.isInsurance, //是否选择了保险
				openId: that.ctkyOpenID,
				totalPrice: that.totalPrice, //总价格
				payParameter: '', //不需要的参数，传空
			
				getOnPoint: that.specialStartStation, //定制班车上车点
				getOffPoint: that.specialEndStation, //定制班车下车点
			};
			console.log(data);
			//--------------------------发起下单请求-----------------------
			uni.request({
				url: 'http://zntc.145u.net/api/ky/SellTicket_NoBill_Booking',
				method: 'GET',
				header: {
					'content-type': 'application/json'
				},
				data: {
					companyCode: companyCode,
					clientID: that.userInfo.userId, //用户ID
					clientName: that.userInfo.nickname, //用户名
					phoneNumber: that.userInfo.phoneNumber, //手机号码

					scheduleCompanyCode: that.orderInfo.scheduleCompanyCode, //班次代码
					executeScheduleID: that.orderInfo.executeScheduleID, //班次ID
					startSiteID: that.orderInfo.startSiteID, //上车点ID
					endSiteID: that.orderInfo.endSiteID, //下车点ID
					startSiteName: that.orderInfo.startStaion, //起点站
					endSiteName: that.orderInfo.endStation, //终点站
					priceID: that.orderInfo.priceID, //价格ID
					setOutTime: setTime, //发车时间
					insuredPrice: that.orderInfo.insurePrice, //保险价格
					carType: that.orderInfo.shuttleType, //班车类型

					fullTicket: that.adultNum, //全票人数
					halfTicket: that.childrenNum, //半票人数
					carryChild: that.childrenNum, //携童人数
					idNameType: that.idNameTypeStr, //乘车人信息
					insured: that.isInsurance, //是否选择了保险
					openId: that.ctkyOpenID,
					totalPrice: that.totalPrice, //总价格
					payParameter: '', //不需要的参数，传空

					getOnPoint: that.specialStartStation, //定制班车上车点
					getOffPoint: that.specialEndStation, //定制班车下车点
				},

				success: (res) => {
					// alert(res);
					if (res.data) {
						if (res.data.status == true) {
							uni.showToast({
								title: res.data.status,
								icon: 'none'
							})
							that.orderNum = res.data.data;
							that.getTicketPaymentInfo(res.data.data);
						} else if (res.data.status == false) {
							uni.hideLoading();
							uni.showModal({
								content: res.data.msg,
								showCancel:false,
								success: (res) => {
									if (res.confirm) {
										uni.navigateBack()
									} 
								}
							})
						}
					}
				},
				fail(res) {
					console.log('失败', res);
					uni.hideLoading();
				}
			})
		},
		//--------------------------获取车票支付参数--------------------------
		getTicketPaymentInfo: function(res) {
			var that = this;
			var timer = null;
			that.timer = timer;
			timer = setInterval(function() {
				uni.request({
					url: 'http://zntc.145u.net/api/ky/SellTicket_Flow',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						//订单编号
						orderNumber: res
					},
					success: (res) => {
						// console.log(res.data);
						if (res.data) {
							if (res.data.status == true) {
								// alert('获取支付参数状态成功',res.data.status)
								var msgArray = JSON.parse(res.data.msg);
								if (msgArray.oldState == '结束') {
									uni.hideLoading();
									uni.showToast({
										title: msgArray.message,
										icon: 'none'
									})
									clearInterval(timer);
								} else if (msgArray.oldState == '支付系统申请支付订单') {
									that.paymentData = msgArray;
									uni.hideLoading();
									uni.showModal({
										content: '请在2分钟内完成支付',
										showCancel: false
									})
									//回调失败，取消定时器
									clearInterval(timer);
								}
							} else if (res.data.status == false) {
								// alert('获取支付参数状态失败',res.data.status)
								var msgArray = JSON.parse(res.data.msg);
								uni.hideLoading();
								uni.showToast({
									title: msgArray.message,
									icon: 'none'
								})
								//回调失败，取消定时器
								clearInterval(timer);
							}
						}
					},
					fail(res) {
						uni.hideLoading();
						console.log('失败');
						//回调失败，取消定时器
						clearInterval(timer);
					}
				})
			}, 3000)
		},
		//--------------------------调起支付--------------------------
		payment: function() {
			var that = this;



























































































		},
		//--------------------------成功之后重新获取车票支付参数--------------------------
		getTicketPaymentInfo_ticketIssue: function(orderNumber) {
			var that = this;
			var timer = null;
			that.timer = timer;
			uni.showLoading({
				title: '检索订单是否支付...'
			});
			timer = setInterval(function() {
				uni.request({
					url: 'http://zntc.145u.net/api/ky/SellTicket_Flow',
					method: 'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						orderNumber: orderNumber,
					},
					success: (res) => {
						console.log('支付参数返回数据', res);
						if (res.data.status == true) {
							uni.hideLoading();
							clearInterval(timer);
							uni.showToast({
								title: '出票成功',
								icon: 'none',
								success(){
									uni.redirectTo({
										url: './CTKYPaySuccess'
									})
								}
							})
							
						} else if (res.data.status == false) {
							clearInterval(timer);
							uni.showToast({
								title: '出票失败',
								icon: 'none',
							})
						}
					},
					fail(res) {
						uni.hideLoading();
						//回调失败，取消定时器
						clearInterval(timer);
					}
				})
			}, 3000)
		},
	}
}
